import React from 'react';
import Timeline from '@mui/lab/Timeline';
import TimelineItem from '@mui/lab/TimelineItem';
import TimelineSeparator from '@mui/lab/TimelineSeparator';
import TimelineConnector from '@mui/lab/TimelineConnector';
import TimelineContent from '@mui/lab/TimelineContent';
import TimelineDot from '@mui/lab/TimelineDot';
import CakeIcon from '@mui/icons-material/Cake';
import SchoolIcon from '@mui/icons-material/School';
import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import WorkIcon from '@mui/icons-material/Work';
import Typography from '@mui/material/Typography';
import PersonIcon from '@mui/icons-material/Person';

const inlineStyles = {
  timelineDotContainer: {
    backgroundColor: '#513F99',
    color: '#ffffff',
  },
};

const CustomizedTimeline = ({ data }) => {
  const currentYear = new Date().getFullYear();
  const turnedEighteenthDate = new Date(data.yearOfBirth);
  turnedEighteenthDate.setFullYear(turnedEighteenthDate.getFullYear() + 18);

  return (
    <Timeline position="alternate" id="timeline" style={{ width: '340px' }}>
      <TimelineItem>
        <TimelineSeparator>
          <TimelineDot style={inlineStyles.timelineDotContainer}>
            <CakeIcon />
          </TimelineDot>
          <TimelineConnector />
        </TimelineSeparator>
        <TimelineContent sx={{ py: '12px', px: 2 }}>
          <Typography variant="h6" component="span" fontWeight="bold">
            Birth
          </Typography>
          <Typography>{data.yearOfBirth.slice(0, 4)}</Typography>
        </TimelineContent>
      </TimelineItem>
      <TimelineItem>
        <TimelineSeparator>
          <TimelineConnector />
          <TimelineDot style={inlineStyles.timelineDotContainer}>
            <SchoolIcon />
          </TimelineDot>
          <TimelineConnector />
        </TimelineSeparator>
        <TimelineContent sx={{ py: '12px', px: 2 }}>
          <Typography variant="h6" component="span" fontWeight="bold">
            10th
          </Typography>
          <Typography>{data.tenthYear}</Typography>
        </TimelineContent>
      </TimelineItem>
      <TimelineItem>
        <TimelineSeparator>
          <TimelineConnector />
          <TimelineDot style={inlineStyles.timelineDotContainer}>
            <SchoolIcon />
          </TimelineDot>
          <TimelineConnector />
        </TimelineSeparator>
        <TimelineContent sx={{ py: '12px', px: 2 }}>
          <Typography variant="h6" component="span" fontWeight="bold">
            12th
          </Typography>
          <Typography>{data.twelvethYear}</Typography>
        </TimelineContent>
      </TimelineItem>
      {data.yearOfBirth && turnedEighteenthDate <= new Date() && (
        <TimelineItem>
          <TimelineSeparator>
            <TimelineConnector />
            <TimelineDot style={inlineStyles.timelineDotContainer}>
              <PersonIcon />
            </TimelineDot>
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent sx={{ py: '12px', px: 2 }}>
            <Typography variant="h6" component="span" fontWeight="bold">
              Turned 18th
            </Typography>
            <Typography>
              {turnedEighteenthDate.getFullYear()}
            </Typography>
          </TimelineContent>
        </TimelineItem>
      )}
{data.yearOfBirth && turnedEighteenthDate > new Date() && (
        <TimelineItem>
          <TimelineSeparator>
            <TimelineConnector />
            <TimelineDot style={inlineStyles.timelineDotContainer}>
              <PersonIcon />
            </TimelineDot>
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent sx={{ py: '12px', px: 2 }}>
            <Typography variant="h6" component="span" fontWeight="bold">
              Turned 18th
            </Typography>
            <Typography>
              {turnedEighteenthDate.getFullYear()}
            </Typography>
            <Typography fontSize={'8px'} color={'red'}>Student is not 18 years of age</Typography>
          </TimelineContent>
        </TimelineItem>
      )}

      {data.graduateYear && (
        <TimelineItem>
          <TimelineSeparator>
            <TimelineConnector />
            <TimelineDot style={inlineStyles.timelineDotContainer}>
              <AccountBalanceIcon />
            </TimelineDot>
          </TimelineSeparator>
          <TimelineContent sx={{ py: '12px', px: 2 }}>
            <Typography variant="h6" component="span" fontWeight="bold">
              Graduate
            </Typography>
            <Typography>{data.graduateYear}</Typography>
          </TimelineContent>
        </TimelineItem>
      )}
      {data.workExperienceYears && (
        <TimelineItem>
          <TimelineSeparator>
            <TimelineConnector />
            <TimelineDot style={inlineStyles.timelineDotContainer}>
              <WorkIcon />
            </TimelineDot>
          </TimelineSeparator>
          <TimelineContent sx={{ py: '12px', px: 2 }}>
            <Typography variant="h6" component="span" fontWeight="bold">
              Work Experience
            </Typography>
            <Typography>{data.workExperienceYears}</Typography>
          </TimelineContent>
        </TimelineItem>
      )}
      <TimelineItem>
        <TimelineSeparator>
          <TimelineConnector />
          <TimelineDot style={inlineStyles.timelineDotContainer}>
            <AccountBalanceIcon />
          </TimelineDot>
        </TimelineSeparator>
        <TimelineContent sx={{ py: '12px', px: 2 }}>
          <Typography variant="h6" component="span" fontWeight="bold">
            College Application
          </Typography>
          <Typography>{data.collegeApplicationYear}</Typography>
        </TimelineContent>
      </TimelineItem>
    </Timeline>
  );
};

export default CustomizedTimeline;
