import React from 'react';
import html2canvas from 'html2canvas';
import { Button } from '@mui/material';

const DownloadTimelineButton = ({ data }) => {
  const handleDownload = () => {
    const timeline = document.getElementById('timeline');
    html2canvas(timeline, {scale: 1.5}).then((canvas) => {
      const link = document.createElement('a');
      link.href = canvas.toDataURL('image/png');
      link.download = `${data.firstName}_${data.lastName}_timeline.png`;
      link.click();
    });
  };

  return (
    <div>
      <Button variant="contained" color="primary" onClick={handleDownload}>Download</Button>
    </div>
  );
};

export default DownloadTimelineButton;
