import React, { useState } from 'react';
import { TextField, Button } from '@mui/material';

const Form = ({ onFormSubmit }) => {
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [yearOfBirth, setYearOfBirth] = useState('');
  const [tenthYear, setTenthYear] = useState('');
  const [twelvethYear, setTwelvethYear] = useState('');
  const [turnedEighteenth, setTurnedEighteenth] = useState(0);
  const [graduateYear, setGraduateYear] = useState('');
  const [workExperienceYears, setWorkExperienceYears] = useState('');
  const [collegeApplicationYear, setCollegeApplicationYear] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();
    const birthDate = new Date(yearOfBirth);
    const turningEighteenthDate = new Date(birthDate.getFullYear() + 18, birthDate.getMonth(), birthDate.getDate());
    const turningEighteenthDateString = turningEighteenthDate.toISOString().split('T')[0];
    setTurnedEighteenth(turningEighteenthDateString);
    onFormSubmit({ firstName, lastName, yearOfBirth, tenthYear, twelvethYear, turnedEighteenth: turningEighteenthDateString, graduateYear, workExperienceYears, collegeApplicationYear });
  };
  

  return (
    <form onSubmit={handleSubmit}>
      <TextField
        label="First Name"
        variant="outlined"
        value={firstName}
        onChange={(e) => setFirstName(e.target.value)}
        margin="normal"
      />
      <TextField
        label="Last Name"
        variant="outlined"
        style={{marginLeft: "10px"}}
        value={lastName}
        onChange={(e) => setLastName(e.target.value)}
        margin="normal"
      />
      <TextField
        label="Date of Birth"
        type="date"
        value={yearOfBirth}
        style={{marginLeft: "10px"}}
        onChange={(e) => {
          setYearOfBirth(e.target.value);
        }}
        required
        InputLabelProps={{
          shrink: true,
        }}
        margin='normal'
      />
      <br />
      <TextField
        label="10th"
        variant="outlined"
        value={tenthYear}
        onChange={(e) => setTenthYear(e.target.value)}
        margin="normal"
      />
      <TextField
        label="12th"
        variant="outlined"
        style={{marginLeft: "10px"}}
        value={twelvethYear}
        onChange={(e) => setTwelvethYear(e.target.value)}
        margin="normal"
      />
      <br/>
      <TextField
        label="Graduate"
        variant="outlined"
        value={graduateYear}
        onChange={(e) => setGraduateYear(e.target.value)}
        margin="normal"
      />
      <TextField
        label="Work Experience Years"
        variant="outlined"
        style={{marginLeft: "10px"}}
        value={workExperienceYears}
        onChange={(e) => setWorkExperienceYears(e.target.value)}
        margin="normal"
      />
      <br />
      <TextField
        label="College Application Year"
        variant="outlined"
        value={collegeApplicationYear}
        onChange={(e) => setCollegeApplicationYear(e.target.value)}
        margin="normal"
      />
      <br />
      <br/>

      <Button type="submit" variant="contained" color="primary">
        Submit
      </Button>
    </form>
  );
};

export default Form;
