import React from 'react';
import html2canvas from 'html2canvas';
import { Button } from '@mui/material';

const DownloadFamilyTreeButton = ({ data }) => {
  const handleDownload = () => {
    const familyTree = document.getElementById('family');
    html2canvas(familyTree, { scale: 0.8 }).then((canvas) => {
      const link = document.createElement('a');
      link.href = canvas.toDataURL('image/png');
      link.download = `${data.name}_family_tree.png`;
      link.click();
    });
  };

  return (
    <div>
      <Button variant="contained" color="primary" onClick={handleDownload}>Download</Button>
    </div>
  );
};

export default DownloadFamilyTreeButton;
