import React, { useState } from 'react';
import TimelineForm from './components/TimelineForm';
import DownloadTimelineButton from './components/DownloadTimelineButton';
import Timeline from './components/Timeline';
import Typography from '@mui/material/Typography';
import FamilyTreeForm from './components/FamilyTreeForm';
import FamilyTree from './components/FamilyTree';
import DownloadFamilyTreeButton from './components/DownloadFamilyTreeButton';
import Grid from '@mui/material/Grid';

const App = () => {
  const [timelineFormData, setTimelineFormData] = useState(null);

  const handleTimelineFormSubmit = (data) => {
    setTimelineFormData(data);
  };

  const [familyTreeFormData, setFamilyTreeFormData] = useState(null);

  const handleFamilyTreeFormSubmit = (data) => {
    setFamilyTreeFormData(data);
  };

  return (
    <>
      <Grid container spacing={2}>
        <Grid item xs={6}>
          <div>
            <Typography variant="h4" gutterBottom style={{ marginTop: "10px" }} fontWeight={"bold"}>
              Timeline Image App
            </Typography>
            <TimelineForm onFormSubmit={handleTimelineFormSubmit} />
            {timelineFormData && (
              <>
                <Timeline data={timelineFormData} />
                <DownloadTimelineButton data={timelineFormData} />
              </>
            )}
          </div>
        </Grid>
        <Grid item xs={6}>
          <div>
            <Typography variant="h4" gutterBottom style={{ marginTop: "10px" }} fontWeight={"bold"}>
              Family Tree Image App
            </Typography>
            <FamilyTreeForm onFormSubmit={handleFamilyTreeFormSubmit} />
            {familyTreeFormData && (
              <>
                <FamilyTree data={familyTreeFormData} />
                <DownloadFamilyTreeButton data={familyTreeFormData} />
              </>
            )}
          </div>
        </Grid>
      </Grid>
    </>
  );
};

export default App;
